.root {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  padding: 25vh 2em;
  background-color: #3d3547;
}

.container {
  position: relative;
  width: 25em;
  margin-right: 10vw;
  flex-shrink: 0;
}

.angleTop {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
}

.headline {
  font-size: 2em;
  color: #a179af;
}

.copy {
  margin: 0 auto;
  flex-grow: 1;
  text-align: left;
  max-width: 500px;
}
.copy code {
  padding: 0.4em 1em;
  font-size: 0.8em;
  color: white;
}

.trianglesPurple {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}