.root {
  background-color: #edeaea;
}

:global body {
  margin: 0;
  font-family: "Roboto Mono", san-serif;
  color: #edeaea;
  font-size: 12px;
  font-weight: 300;
}
:global h1,
:global .h1 {
  text-transform: uppercase;
  font-size: 4em;
  font-weight: 700;
  font-style: italic;
  letter-spacing: 0.3em;
  color: #3d3547;
}
:global code {
  display: inline-block;
  margin: 0 0.4em 0 0;
  font-family: "Roboto Mono", san-serif;
  background-color: #564b64;
  padding: 0.2em 0.4em;
  border-radius: 3px;
}
:global p {
  margin: 2em 0;
  font-size: 1.2em;
  line-height: 1.6;
  color: #cac1c1;
}
:global * {
  box-sizing: border-box;
}
:global a {
  color: #edeaea;
  text-decoration: none;
  font-weight: bold;
}
:global a:hover {
  color: #a179af;
}
:global hr {
  width: 100%;
  height: 3em;
  background-color: #fff;
  border: none;
}
:global .btn {
  display: inline-block;
  border: 2px solid #a179af;
  padding: 0.6em 1em;
  color: #a179af;
  font-weight: 500;
  margin-right: 1em;
}
:global .btn:hover {
  background-color: #a179af;
  color: #edeaea;
}
:global .visually-hidden {
  position: absolute;
  top: 0;
  left: 0;
  height: 0;
  width: 0;
  overflow: hidden;
}